import { data } from 'jquery'
import {React, useState} from 'react'
import Button from'./Button'
import DisplayFitness from './DisplayFitness'


const Fitness = () => {


    const [form, setForm] = useState({
        activity:"",
    })
    const [activity, setActivity] = useState([])

    async function activityData(e) {

        e.preventDefault()
            console.log(form.activity);
       
            // Simple POST request with a JSON body using fetch
            const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json', 'x-app-id' : '04a369a1', 'x-app-key': 'e4b8ebfb9df17e6ac86bc8df36322113' },
                body: JSON.stringify({
                    "query":`${form.activity}`,
                    "gender":"female",
                    "weight_kg":72.5,
                    "height_cm":167.64,
                    "age":30
                   })
            };
            const data = await fetch('https://trackapi.nutritionix.com/v2/natural/exercise', requestOptions)

           const response = await data.json()

          

            
            
        
            setActivity ({data: response})
            
            
    }

    const handleChange = (e) => {
        let name = e.target.name;
        let value = e.target.value;
        if (name == 'activity'){
            setForm({...form, activity:value})
        }
    }
    return (
        <div>

            <form>

                <h3> Enter Activity:  </h3>

                <input type="text" name="activity" placeholder= "Activity" onChange = { e => handleChange(e)}/>
                &nbsp;&nbsp;&nbsp;&nbsp;

                <Button color= 'lavender' text="Submit" onClick= {e => activityData(e)} />

               
             
            </form>

            {activity.data != undefined ? ( 
        <div>
           <DisplayFitness data = {activity.data} /> 
        
          
          
        </div>
        ) : null} 
    </div>

   
  )
}
 
export default Fitness