
import React, { useState, useRef } from 'react';
import { Modal, Form, Button, Card, Alert } from 'react-bootstrap'
import { useAuth } from '../contexts/AuthContext'
import Header from './Header'
import 'bootstrap/dist/css/bootstrap-reboot.css'
import { Link, useHistory } from 'react-router-dom';




const Signup = () => {
    const emailRef = useRef()
    const passwordRef = useRef()
    const passwordConfirmRef = useRef()
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const {signup} = useAuth()
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()
    
    async function handleSubmit(e) {
        e.preventDefault()

        if (passwordRef.current.value !== passwordConfirmRef.current.value){
            return setError('Passwords do not match')
        }

        try {
            setError("")
            setLoading(true)
            await signup(emailRef.current.value, passwordRef.current.value)
            history.push("/")
        } catch {
            setError('Failed to create an account')
        }
        setLoading(false)  
    }

    
    

    return (
        <>
            <h1> Welcome to My Wedding Planner!</h1>
                    <Card>
                       
                        <Card.Body>
                        <p> Welcome to My Wedding Planner!</p>
                
                            <h5 className="text-center mb-4 "> Sign Up</h5>
                            {error && <Alert variant= "danger">{error}</Alert>}
                            <Form onSubmit = {handleSubmit}>
                                <Form.Group id="email">
                                    <Form.Label className = "">Email</Form.Label>
                                    <Form.Control type="email" ref={emailRef} required></Form.Control>
                                </Form.Group>
                                <Form.Group id="password">
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control type="password" ref={passwordRef} required></Form.Control>
                                </Form.Group>
                                <Form.Group id="password-confirm">
                                    <Form.Label>Password Confirmation</Form.Label>
                                    <Form.Control type="password" ref={passwordConfirmRef} required></Form.Control>
                                </Form.Group>
                                <Button variant= "secondary" disabled={loading} className="w-100" type="submit">
                                    Sign Up
                                </Button>
                            </Form>

                        </Card.Body>
                    </Card>

                
                
                    <Button variant="danger" onClick={handleClose}>
                        Close
                </Button>
                <div className = "w-100 text-center mt-2">
                        Need an account? <Link to= "/login"  style ={{color: 'blue'}}> Log In</Link>
                    </div>
                  
                
        
        </>
    )
}

export default Signup;