import React from 'react'

function DisplayNutrition(props) {
    const {data} = props
    console.log(data)

    let cal = 0
    for (let i = 0; i < data.foods.length; i ++) {
        cal += data.foods[i].nf_calories
    }
    
    return (
        <div>
            
            <span>      
                You consumed {cal} calories.
            </span>
           
        </div>
    )
}

export default DisplayNutrition