import {React, useState} from 'react'
import Button from'./Button'
import DisplayNutrition from './DisplayNutrition'


const Nutrition = () => {


    const [form, setForm] = useState({
        foods:"",
    })
    const [nutrition, setNutrition] = useState([])

    async function nutritionData(e) {

        e.preventDefault()
            console.log(form.foods);
       
            // Simple POST request with a JSON body using fetch
            const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json', 'x-app-id' : '04a369a1', 'x-app-key': 'e4b8ebfb9df17e6ac86bc8df36322113' },
                body: JSON.stringify({
                    "query": `${form.foods}`,
                    "timezone": "US/Eastern"
                   })
            };
            const data = await fetch('https://trackapi.nutritionix.com/v2/natural/nutrients', requestOptions)
            .then((res) => res.json())
            .then((data) => data)
        
                setNutrition ({data: data})
    }

    const handleChange = (e) => {
        let name = e.target.name;
        let value = e.target.value;
        if (name == 'foods'){
            setForm({...form, foods:value})
        }
    }
    return (
        <div>

            <form>
                <h2>Wanna get that dream wedding bod faster? </h2> 
                <h5> Track your nutrition and fitness with our in built calorie claculator! Just type in what food you ate and what activities you did and voila!</h5>
                <h3> Enter food:  </h3>
                <input type="text" name="foods" placeholder= "Food" onChange = { e => handleChange(e)}/>
                &nbsp;&nbsp;&nbsp;&nbsp;

                <Button color= 'lavender' text="Submit" onClick= {e => nutritionData(e)} />
             
            </form>

            {nutrition.data != undefined ? (
        <div>
          <DisplayNutrition data={nutrition.data} />
        </div>
      ) : null}
    </div>
  )
}
 
export default Nutrition