import React from 'react'

function DisplayWeather(props) {
    const {data} = props
    console.log(data)
    
    return (
        <div>
            <span>
                
                As of {new Date().toLocaleTimeString()} the temperature in {data.name} is {Math.floor(1.8* ((data.main.temp) - 273) + 32)} °F.
        

            </span>
           
        </div>
    )
}

export default DisplayWeather
